		data() {
			return {
				StatusBar: this.StatusBar,
				CustomBar: this.CustomBar,
				menuBorder: false,
				menuArrow: false,
				menuCard: false,
				
				agent_id:'',
				token:'',
				name:'',
				tel:'',
				nackName:'',
				sex:'',
				email:'',
				qq:'',
				area:'',
				address:'',
				list:[],
			};
		},
		onLoad() {
			const value = uni.getStorageSync('agentInfo');
			if (value) {
				this.token=value.token;
			}
			uni.request({
				url: 'http://192.168.0.199:8080/agent/agent/ajax-agent-info',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				method: 'POST',
				dataType: 'json',
				cache: false,
				data: {
					token:this.token,
				},
				success: res => {
					this.list=res;
					let data=this.list.data;
					if(data.code==200){
						this.name=data.data.agent_name;
						this.tel=data.data.agent_tel;
						this.nackName=data.data.nickname;
						if(data.data.sex=='0'){
							this.sex='男';
						}else if(data.data.sex=='1'){
							this.sex='女';
						}
						this.agent_id=data.data.agent_id;
						this.email=data.data.email;
						this.qq=data.data.qq;
						this.area=data.data.area;
						this.address=data.data.address;
					}else if(data.code==-200){
						uni.showModal({
								showCancel:false,
								content: '用户信息已失效，请重新登陆',
								success: function (res) {
									if (res.confirm) {
											uni.redirectTo({
												url: '../login/login'
											});
									}
								}
							});
					}
				},
				fail: () => {
					uni.showToast({
						icon: 'none',
						title: '网络异常,请稍后重试'
					});
				},
				complete: () => {}
				});
		},
		methods: {
			ToPage: function() {
					uni.redirectTo({
						url: '../about/editinfo'
					});
			},
			navTo:function(){
				uni.redirectTo({
					url: '../tabbar/tabbar'
				});
			}
		}
	}
